<template>
  <div id="Graph">
    <h1>DeepTraLog</h1>
    <div id="dropdown-container">
      <label for="select-option">Select Trace:</label>
      <select id="select-option"></select>
      <div class="dropdown-spacing" style="width:10px;"></div>
      <label for="select-option-layout">Layout:</label>
      <select id="select-option-layout">
        <option value="network">Network Layout</option>
        <option value="trace" selected>Trace Layout</option>
      </select>
    </div>
  </div>
</template>
<script>
import * as d3 from 'd3'
export default {
  data() {
    return {
    }
  },
  mounted() {
    let margin = {top: 10, right: 25, bottom: 30, left: 40}
    let width = 1400 - margin.left - margin.right
    let height = 650 - margin.top - margin.bottom
    const files = ['single_graph2.json', 'id_service.csv', 'id_url+temp.csv', 'id_url+type.csv', 'all_graph_12.json'];

    let svg = d3.select("#Graph")
        .append("svg")
          .attr("width", width + margin.left + margin.right)
          .attr("height", height + margin.top + margin.bottom)
    let zoom_g = svg.append("g")
          .attr("class", "zoom")
          .attr("transform",
                "translate(" + margin.left + "," + margin.top + ")");
    
    const promises = files.map(file => {
      if (file.endsWith('.json')) {
        return d3.json(file);
      } else if (file.endsWith('.csv')) {
        return d3.csv(file);
      }
    })
    Promise.all(promises)
      .then(dataArray => {
        handleData(dataArray);
      })
      .catch(error => console.error('Error loading files:', error));

    // 处理数据的函数
    function handleData(data) {
      let single_graph_data = data[4].find(function(data) {
            return data.error_trace_type === 'F01-01';
        });
        
      // eslint-disable-next-line no-unused-vars
      let id_service_data = data[1]
      // eslint-disable-next-line no-unused-vars
      let id_url_temp_data = data[2]
      // eslint-disable-next-line no-unused-vars
      let id_url_type_data = data[3]
      // eslint-disable-next-line no-unused-vars
      let all_graph_data = data[4]

      all_graph_data.sort(sortTrace);
      console.log(data)

      const colors = [
          '#fdeff2', '#f6bfbc', '#efab93', '#f0908d', '#f3f3f3', '#c0a2c7',
          '#e597b2', '#e6cde3', '#8d6449', '#bf794e', '#b79b5b', '#e6b422',
          '#6e7955', '#888e7e', '#00a381', '#00a497', '#478384', '#5b7e91',
          '#89c3eb', '#84a2d4', '#b8d200', '#f8b862', '#9d5b8b', '#c4a3bf',
          '#9fa0a0', '#c0a2c7', '#4d5aaf', '#82ae46', '#f8f4e6', '#59b9c6',
          '#74325c', '#d2691e', '#99ffff', '#99ff33', '#a5c9c1', '#e29399'
      ];
      let selectedNodes = new Map();
      let allNodes = [];
      function updateSVG(single_graph_data, id_service_data, id_url_temp_data, id_url_type_data) {
        zoom_g.selectAll("*").remove()
        d3.select("#Graph").selectAll(".legend-container").remove()
        d3.select("#Graph").selectAll(".button").remove()
        let scoresArray = single_graph_data.nodes_score.match(/\d+\.\d+/g).map(Number);
        let minScore = Math.min(...scoresArray);
        let maxScore = Math.max(...scoresArray);
        let normalizedArray = scoresArray.map(score => (score - minScore) / (maxScore - minScore));

        // 将每个数组项依次插入到single_graph_data.node_info[i]
        normalizedArray.forEach((score, i) => {
          single_graph_data.node_info[i].push(score.toFixed(2));
        });

        let edgeData = single_graph_data.edge_index.map((edge, index) => {
          return {
            source: edge[0],
            target: edge[1],
            type: single_graph_data.edge_attr[index]
          };
        });

        let serviceNameColorArray = [];

        const colorScale = d3.scaleSequential(d3.interpolateYlOrRd);

        let nodeData = single_graph_data.node_info.map((node, index) => {

          const serviceName = id_service_data[node[1]].Service;

          const color = colors[node[1]];
          
          const existingService = serviceNameColorArray.find(entry => entry.ServiceName === serviceName);

          if (!existingService) {
            serviceNameColorArray.push({
              ServiceName: serviceName,
              color: color
            });
          }
          
          return {
            Temp: node[0],
            Service: node[1],
            ServiceName: id_service_data[node[1]].Service,
            q: node[2],
            w: node[3],
            e: node[4],
            Type: node[5],
            TempType: node[6],
            Id: index,
            color: colors[node[1]],
            scoreColor: colorScale(node[7]),
            Content: id_url_type_data[node[6]].Content,
            score: node[7],
            show: true
          };
        });
        allNodes = []
        allNodes = allNodes.concat(nodeData.slice());
        // eslint-disable-next-line
        let drag = d3.drag()
          .on('start', dragstarted)
          .on('drag', dragged)
          .on('end', dragended);

        function zoomed({ transform }) {zoom_g.attr("transform", transform)}
        svg.call(d3.zoom().scaleExtent([0.2, 6]).on("zoom", zoomed));

        let tooltip = d3.select("#Graph")
          .append("div")
          .style("opacity", 0)
          .attr("class", "tooltip")
          .style("background-color", "white")
          .style("border", "solid")
          .style("border-width", "2px")
          .style("border-radius", "5px")
          .style("padding", "5px")
        
        let simulation = d3.forceSimulation(nodeData)
          .force('link', d3.forceLink(edgeData).id(d => { return d.Id }))
          .force('charge_force', d3.forceManyBody().strength(-800))
          .force('center_force', d3.forceCenter(width / 2, height / 2))
          .force('collide', d3.forceCollide().radius(20).strength(0.5)) // 添加碰撞力
          // .force("x", d3.forceX())
          .force("y", d3.forceY());
        
        zoom_g.append("defs").append("marker")
          .attr("id", "arrowhead")
          .attr("refX", 6 + 15) // Controls the shift of the arrowhead along the path
          .attr("refY", 2)
          .attr("markerWidth", 6)
          .attr("markerHeight", 4)
          .attr("orient", "auto")
          .append("path")
          .attr("d", "M 0,0 V 4 L6,2 Z"); // Arrowhead path

        let links = zoom_g.append('g')
          .attr('class', 'links')
          .selectAll('line')
          .data(edgeData)
          .enter()
          .append('line')
          .attr('stroke-width', 3)
          .style('stroke', d => {if(d.type === 0) {return 'black'} else if(d.type === 1) {return 'red'} else if (d.type === 2) return 'green'})
          .attr("marker-end", "url(#arrowhead)");

        
        let nodes = zoom_g.append('g')
          .attr('class', 'nodes')
          .selectAll('circle')
          .data(nodeData)
          .enter()
          .append('circle')
          // .attr('r', d => `${d.score * 15+3}`)
          .attr('r', 15)
          .attr('fill', d => {if(d.score > 0.6) {return d.scoreColor} else {return "#62B1D0"}})
          .style('stroke-width', '2px')
          .on('mouseover', showTooltip)
          .on('mouseout', hideTooltip)
          .on('mousemove', moveTooltip)
          .on('click', function() {
              const clickedNode = d3.select(this).data()[0].Id;
              // 检查节点是否已被选中
              const isSelected = selectedNodes.has(clickedNode);

              // 如果节点已被选中，则从数组中移除
              if (isSelected) {
                selectedNodes.delete(clickedNode);
                d3.select(this).style('stroke', 'none')
              } else {
                // 否则，将节点添加到数组中
                selectedNodes.set(clickedNode, nodeData[clickedNode]);
                d3.select(this).style('stroke', 'red')
              }
            })
          .call(drag); // 将拖拽事件应用到节点

        let nodeLabels = zoom_g.selectAll(".node-label")
          .data(nodeData)
          .enter().append("text")
          .attr("class", "node-label")
          .attr("class", "unselectable")
          .text(d => d.TempType)
          .style("text-anchor", "middle") // 设置文本锚点为中心
          .style("dominant-baseline", "middle") // 设置基线为中心
          .style("font-size", "12px")
          .style("pointer-events", "none")
        
        let nodeBlocks = zoom_g.selectAll(".node-label")
          .data(nodeData)
          .enter().append("text")
          .attr("class", "node-label")
          .attr("class", "unselectable")
          .text("●")
          .style("fill", d => d.color)
          .style("text-anchor", "middle") // 设置文本锚点为中心
          .style("dominant-baseline", "middle") // 设置基线为中心
          .style("font-size", "30px")
          .style("pointer-events", "none")

        let legendContainer = d3.select("#Graph").append('div').attr('class', 'legend-container');

        legendContainer.selectAll('.legend')
          .data(serviceNameColorArray)
          .enter().append('button')
          .attr('class', 'legend')
          .style('color', d => d.color)
          .style('display', 'inline')
          .style('top', '20px')
          .style('right', '10px')
          .on("click", d => {nodes.filter(dd => dd.ServiceName === d.target.__data__.ServiceName).style('opacity', 0)
          })
          .html(d => `<span class="legend-color">&#9632;</span> ${d.ServiceName}`);

        let serviceXMap = new Map()
        if(d3.select("#select-option-layout").property("value") === 'network') {
          simulation.force("x", d3.forceX())
        }else if(d3.select("#select-option-layout").property("value") === 'trace') {
          nodeData.forEach(node => {
            const serviceName = node.ServiceName;
            if (!serviceXMap.has(serviceName)) {
              // 如果映射中没有该服务，则为其分配一个新的x坐标值
              serviceXMap.set(serviceName, serviceXMap.size * 50); // 这里假设每个服务间的间距为100
            }
            // 为节点设置x坐标
            node.x = serviceXMap.get(serviceName);
         });
        }


        edgeData.forEach(edge => {
            if(nodeData[edge.source.Id].y > nodeData[edge.target.Id].y) {
              nodeData[edge.target.Id].y = nodeData[edge.source.Id].y + 50
            }
        })


        simulation.on('tick', () => {
          nodes
            .attr("cx", d => d.x)
            .attr("cy", d => d.y);

          links
            .attr("x1", d => d.source.x)
            .attr("y1", d => d.source.y)
            .attr("x2", d => d.target.x)
            .attr("y2", d => d.target.y);

          nodeLabels
            .attr("x", d => d.x)
            .attr("y", d => d.y-20);
          nodeBlocks
            .attr("x", d => d.x)
            .attr("y", d => d.y);
        })



        function dragstarted(event, d) {
          if (!event.active) simulation.alphaTarget(0.1).restart();
          d.fx = d.x;
          d.fy = d.y;
        }

        function dragged(event, d) {
          d.fx = event.x;
          d.fy = event.y;
        }

        function dragended(event, d) {
          if (!event.active) simulation.alphaTarget(0);
          d.fx = null;
          d.fy = null;
        }
        // 当鼠标悬停在节点上时显示浮动信息框
        function showTooltip() {
          tooltip
            .style("opacity", 1)
            
          d3.select(this)
            .style("opacity", 1)
        }

        // 当鼠标移开节点时隐藏浮动信息框
        function hideTooltip() {
          tooltip
            .style("opacity", 0)
            .style("left", 0)
            .style("top", 0)
          d3.select(this)
            .style("opacity", 1)
        }
        function moveTooltip(event, d) {
          tooltip
            .style("left", `${event.clientX+50}px`)
            .style("top", `${event.clientY-19.5}px`)
            .html(`Content :  ${d.Content.substring(0, 300)} ; Anomaly Score : ${d.score}`)
        }
      }

      updateSVG(single_graph_data, id_service_data, id_url_temp_data, id_url_type_data)

      d3.select("#select-option")
        .selectAll("option")
        .data(all_graph_data)
        .enter()
        .append("option")
        .text(d => d.error_trace_type)
        .property("value", d => d.error_trace_type)
      d3.select("#select-option")
        .on("change", onChange);

      d3.select("#select-option-layout")
        .on("change", onChangeLayout);

      d3.select("#Graph")
        .append("button")
        .text("Export") // 设置按钮文本
        .on("click", handleClick); // 添加点击事件监听器
      
      function sortTrace(a, b) {
          const typeA = a.error_trace_type.toUpperCase();
          const typeB = b.error_trace_type.toUpperCase();
          if (typeA < typeB) {
              return -1;
          }
          if (typeA > typeB) {
              return 1;
          }
          return 0;
      }

      function onChange() {
        var selectedValue = d3.select(this).property("value");
        single_graph_data = all_graph_data.find(function(data) {
            return data.error_trace_type === selectedValue;
        });
        updateSVG(single_graph_data, id_service_data, id_url_temp_data, id_url_type_data)
      }
      function onChangeLayout() {
        var selectedValue = d3.select("#select-option").property("value");
        single_graph_data = all_graph_data.find(function(data) {
            return data.error_trace_type === selectedValue;
        });
        updateSVG(single_graph_data, id_service_data, id_url_temp_data, id_url_type_data)
      }

      function handleClick() {
        // console.log(Array.from(selectedNodes.values()))
        if(selectedNodes.size === 0) {
          const formattedStrings = allNodes.map(node => `${node.ServiceName} || ${node.Content} || ${node.score}`);
          console.log(formattedStrings.join('\n'))
        }else {
          const formattedStrings = Array.from(selectedNodes.values()).map(node => `${node.ServiceName} || ${node.Content} || ${node.score}`);
          console.log(formattedStrings.join('\n'))
        }
        
        
        // const resultString = Array.from(selectedNodes.values()).join('\n');
        // console.log(resultString)
      }
    }
  },
  methods: {
  }
}
</script>
<style>
#Graph {
  position: relative;
}
.links line {
  stroke: #999;
  stroke-opacity: 0.6;
}

.tooltip {
  display: inline;
  position: fixed;
}

.nodes circle {
  stroke: #fff;
  stroke-width: 1.5px;
}

.unselectable {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

svg {
  border: 1px solid #ccc;
}
</style>
 